.post{
    width: 500px;
    margin: 0px 25px 40px 25px;
    
}
.postImg{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: 10px;

}

.postInfo{
     display: flex;
     flex-direction: column;
     align-items: center;
     
}
.postCat{
    font-family: 'Varela Round', sans-serif;
     font-size: 11px;
     color: #ffffff;
     line-height: 20px;
     margin-top: 15px;
     margin-right: 10px;
     cursor: pointer;
}

.postTitle{
     font-family: 'Josefin Sans', sans-serif;
     font-size: 24px;
     font-weight: 700;
     color:rgb(243, 238, 231);
     margin-top: 15px;
     cursor: pointer;
    
}
 .postDate{
      font-family: 'Lora', serif;
     font-style: italic;
     font-size: 13px;
     color: rgb(153, 148, 148);
     margin-top: 15px;

 }

 .postDesc{
      font-family: 'Varela Round', sans-serif;
      font-size: 14px;
      color: rgb(221, 215, 215);
      line-height: 24px;
      margin-top: 15px; 
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
 }

 